import * as api from '../api';
import {fetchAvailabilityById} from "../api";
import {fetchCar} from "../api";

function fetchTasksSucceeded(cars) {
  return {
    type: 'FETCH_CARS_SUCCEEDED',
    payload: {
      cars,
    },
  };
}

function fetchTasksFailed(error) {
  return {
    type: 'FETCH_CARS_FAILED',
    payload: {
      error,
    },
  };
}

function fetchTaskAvailabilityStarted() {
  return {
    type: 'FETCH_CARS_STARTED',
  };
}

function fetchTaskAvailabilitySucceeded(carAvailability) {
  return {
    type: 'FETCH_CARS_AVAILABILITY_SUCCEEDED',
    payload: {
      carAvailability,
    },
  };
}

function fetchTaskAvailabilityFailed(error) {
  return {
    type: 'FETCH_CARS_FAILED',
    payload: {
      error,
    },
  };
}

function fetchTasksStarted() {
  return {
    type: 'FETCH_CARS_STARTED',
  };
}

export function fetchCarAvailabilityById(id) {
  return dispatch => {
    return fetchAvailabilityById(id)
      .then(resp => resp.data)
      .then(data => {
        dispatch(fetchTaskAvailabilitySucceeded(data))
      })
  }
}

export function fetchCars() {
  return dispatch => {
    dispatch(fetchTasksStarted());

    return api
      .fetchCar()
      .then(resp => resp.data)
      .then(data => dispatch(fetchTasksSucceeded(data)))
      .catch(err => {
        dispatch(fetchTasksFailed(err.message));
      });
  };
}

export function fetchCarAndAvailability() {
  return (dispatch, getState) => {
    dispatch(fetchCars())
      .then(() => {
        const abc = getState().cars.cars;

      })
  }
}



