const initialState = {
  cars: [],
  carAvailability: [],
  isLoading: true,
  error: null,
};

export default function tasks(state = initialState, action) {
  switch (action.type) {
    case 'FETCH_CARS_STARTED':
      return {
        ...state,
        isLoading: true,
      };

    case 'FETCH_CARS_SUCCEEDED':
      return {
        ...state,
        cars: action.payload.cars,
        isLoading: false,
      };

    case 'FETCH_CARS_AVAILABILITY_SUCCEEDED':
      return {
        ...state,
        carAvailability: action.payload.carAvailability
      };

    case 'FETCH_CARS_FAILED':
      return {
        ...state,
        isLoading: false,
        error: action.payload.error,
      };

    default: {
      return state;
    }
  }
}



