import React, {Component} from 'react';
import {Card, CardBody, CardHeader, CardFooter, ImageHeader} from '../../components/Card';
class CarLists extends Component {
  constructor(props) {
    super(props);

  }

  render() {
    console.log('carlist', this.props.cars);
    if(this.props.isLoading){
      return (
        <div>loading ....</div>
      );
    }
    return (
      <div className='wrapper'>
        {this.props.cars.map(x =>{
          return(
            <Card>
              <CardHeader><p>{x.name}</p></CardHeader>
              <ImageHeader imageUrl={x.img}/>
              <CardBody><p>{x.make}</p></CardBody>
              <CardFooter>{x.model}</CardFooter>
            </Card>
          );
        })}
      </div>
    );
  }
}

export default CarLists;



